.main {
   flex-grow: 1;
   z-index: 1;

   &__wrapper{
      width: 100%;
      height: 100%;   
      display: flex;
      align-items:center;
   }

   &__inner{
      width: 100vw;
      height: 520px;
   }

   &__inner-pg1{
      display: flex;
      height: 100%;
      width: 100%;

   }

   &__title{
      margin: auto;
      padding-bottom: 30px;
      max-width: 245px;
      font-family: OpenSans-Bold;
      font-size: 72px;
      line-height: 80px;
      color: #1F2939;
   }

   &__content{
      
   
      width: 100%;
      display: flex;
      position: relative;
   }

   &__backgr-img{
      background-image: url(../images/backgr.png);
      background-repeat: no-repeat;
      
      background-size: 46%;
      
      width: 100%;
      height: 100%;
      position: absolute;
      top: -10%;
      left: 37%;
      
   }

   &__btn{
      position: absolute; 
      z-index: 289;
      left: 22%; 
      top: 55%; 
      transform: translate(-50%, -50%);
      @include reset-button;
      width: 180px;
      height: 180px;
      border-radius: 90px;
        
      background: #5243C2;
      box-shadow: 0 0 0 0  #5243C2;
      animation: pulse 3s infinite;
       }

       @keyframes pulse {
         0% {
           transform: scale(0.95);
           box-shadow: 0 0 0 0 #5243C2;
         }
         
         70% {
           transform: scale(1);
           box-shadow: 0 0 0 10px rgba(255, 121, 63, 0);
         }
         
         100% {
           transform: scale(0.95);
           box-shadow: 0 0 0 0 rgba(255, 121, 63, 0);
         }
       
   }

   &__btn-text{
      font-family: OpenSans-ExtraBold;
      font-size: 24px;
      line-height: 30px;
      color: #FFFFFF;

      width: 98px;
      height: 60px;

      text-align: center;
      text-transform: uppercase;

      margin: 0 auto;
   }

   &__burger{
      padding-left: 100px;
      width: 100%;
      display: flex;
      align-items: center;
   }
   ////////////////////////////////////////////////////
   &__inner-pg2{
      
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      width: 100%;
      position: relative;
      
   }

   &__bg-img2{
      background-image: url(/src/images/bg2.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 30%;
      top: -22%;
      left: -1%;
      height: 100%;
      width: 100%;
      position: absolute;
   }

   &__topside{
      display: flex;
      justify-content: space-between;
      height: 340px;
      position: relative;
      top: 6%;
   }

   &__title-2{
      
      padding-top: 50px;
      max-width: 360px;
      font-family: OpenSans-Bold;
      font-size: 72px;
      line-height: 80px;
      color: #1F2939;
   }

   &__content-wrapper{
      display: flex;
      // margin-right: 200px;
      

   }
   
   &__const{
      width: 500px;
      
      display: flex;
      flex-direction: column;
      flex-flow: column-reverse;
   }

   &__const-inner{
      position: relative;
      width: 100%;
      height: 135px;
      background-image: url(/src/images/default__land.svg);
      background-position: center;
      background-repeat: no-repeat;
      display: block;
      margin: 0 auto;
   }

   &__info{
      width: 405px;
   }

   

   ////////////////////////////////////////////////////

   &__info{
      display: flex;
      flex-direction: column;
   }

   &__info-title{
      margin: 0;
      height: 120px;
      font-family: OpenSans-Semibold;
      font-size: 36px;
      line-height: 49px;
      color: #1F2939;
      &::after{
         content: url(/src/images/divider.png);
         
      }
   }

   &__info-sum{
      display: flex;
      justify-content: space-between;
   }

   &__total{
      font-family: OpenSans-ExtraBold;
      font-size: 36px;
      line-height: 49px;
      color: #5243C2;
   }

   &__btn-checkout{
      @include reset-button;
      width: 192px;
      height: 52px;  
      background: #5243C2;
      border-radius: 99px;
   }

   &__btn-checkout-text{
      font-family: OpenSans-Semibold;
      font-size: 16px;
      line-height: 20px;
      color: #F5F5FF;
   }

   &__about-gift{
      font-family: OpenSans-Regular;
   }

   &__reason-sum{
      color: #FF4D4F;
      font-family: OpenSans-Semibold;
   }

   &__extra-info{
      display: flex;
      justify-content: space-between;
      width: 405px;
      height: 64px;
      background: #F5F5FF;
      border-radius: 32px;
      align-items: center;
   }

   &__time{
      font-family: OpenSans-SemiBold;
      font-size: 14px;
      line-height: 20px;
      color: #1F2939;
      padding-left: 56px;
      width: 124px;
      position: relative;
      &::before{
         content: url(/src/images/time.svg);
         margin-right: 8px;
         height: 32px;
         position: absolute;
         top: -25%;
         left: 12%;
      }
   }

   &__capacity{
      font-family: OpenSans-SemiBold;
      font-size: 14px;
      line-height: 20px;
      color: #1F2939;
      padding-left: 50px;
      position: relative;
      width: 124px;
      &::before{
         content: url(/src/images/oz.svg);
         margin-right: 8px;
         position: absolute;
         top: -25%;
         left: 5%;
      }
   }

   &__kcal{
      font-family: OpenSans-SemiBold;
      font-size: 14px;
      line-height: 20px;
      color: #1F2939;
      padding-left: 40px;
      position: relative;
      width: 124px;
      &::before{
         content: url(/src/images/kcal.svg);
         margin-right: 8px;
         position: absolute;
         top: -25%;
         left: -1%;
      }
   }


   &__botside{
      margin: 0 auto;
      height: 180px;
      width: 1344px;
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 70%;
      left: 4%;
      
   }

   &__ingradient{
      height: 180px;
      width: 140px;
      border: 1px solid #F0F7FA;
      border-radius: 32px;
      opacity: 0;
      transition: all 0.2s ease-out;
      // background-color: rgb(0, 255, 89, 0.2);
      // background-color: rgb(255, 17, 0, 0.4);
   }

   &__ingradient-inner{
      padding: 24px 18px;
      height: 180px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   }

   &__ingradient-img{
      width: 104px;
      height: 50px;
   }

   

   

   &__ingradient-name{
      text-align: center;
      font-family: OpenSans-SemiBold;
      font-size: 14px;
      line-height: 20px;
      color: #1F2939;
   }

   &__counter{
      display: flex;
      justify-content: space-between;
      width: 104px;
   }

   &__counter-minus{
      @include reset-button;
      margin: auto 0;
      width: 28px;
      height: 28px;
      background: #F5F5FF;
      border-radius: 99px; 
      position: relative;
      &::after{
         content: url(/src/images/minus.svg);
         position:absolute;
         top: 4px;
         left: 8px;
      }
   }

   &__counter-plus{
      @include reset-button;
      margin: auto 0;
      width: 28px;
      height: 28px;
      background: #F5F5FF;
      border-radius: 99px;
      position: relative;
      &::before{
         content: url(/src/images/plus.svg);
         position:absolute;
         top: 7px;
         left: 9px;
      }
   }

   &__count{
      font-family: OpenSans-SemiBold;
      font-size: 20px;
      line-height: 32px;
      color: #1F2939;
   }


}