.item__cutlet{
      width: 100%;
      height: 130px;
      background-image: url(/src/images/Type=cutlet.svg);
      background-position: center;
      background-repeat: no-repeat;
      display: block;
      position: absolute;
      transition: all 0.1s ease-in-out;
      
      
}

@keyframes ani {
0%    {opacity: 0.8;}
100%  {opacity: 1; }
}

@keyframes aniBackLeft {
0%    {opacity: 1; left: 0%}
100%  {opacity: 0; left: -30%}
}

@keyframes aniBackRight {
      0% {opacity: 1; left: 0%}
      100% {opacity: 0; left: +30%}
      }

.for_start_ani{
      animation: ani 0.2s forwards;
}

.for_del_ani_left{
      animation: aniBackLeft 0.2s forwards;
}

.for_del_ani_right{
      animation: aniBackRight 0.2s forwards;
}

